import {Component} from '@angular/core';
import {Alert, AlertController, IonicPage, ModalController, NavController, NavParams, Platform} from 'ionic-angular';
import {FunctionsProvider} from "../../providers/functions/functions";
import {AngularFireAuth} from 'angularfire2/auth';
import {Storage} from "@ionic/storage";
import {ModalOption} from "./modal-options.component";
import {Api} from "../../providers/api/api";


@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',


})
export class HomePage {
    private uid: any ;
    private bid: any;
    private logged: any;
    private  pageId: any

  constructor(public navCtrl: NavController,private func:
                  FunctionsProvider,
              private navParams:NavParams,
              private churchname:Api,
              private afs: AngularFireAuth,
              private alertCtrl: AlertController,
              private storage: Storage,
              private modalCtrl: ModalController,
              private platform:Platform,

  ) {
      this.refresh()


       this.pageId = this.navParams.get('pageId');
      switch(this.pageId){
          case "New Devotion" : this.dv();
          break;
          case "New Event": this.evn();
          break;
          case "New Notice": this.nt();
          break;
          case "New Project":this.navCtrl.push('HomeTabsPage');
          break;
          case  this.churchname.churchname +" is Live":this.navCtrl.push('LivePage');
          break;
          case  this.churchname.churchname +" is going live soon":this.navCtrl.push('LivePage');
          break;
          case  "New Podcast" :this.navCtrl.push('AudioPage');
              break;
          case "New Video" :this.navCtrl.push('LivestreamPage');
              break;

      }

     // switch this.pageId
      this.storage.get('login').then((val) => {
          this.logged = val


          this.afs.authState.subscribe(res => {

              if (res && res.uid && this.logged == "true") {
                  this.func.sync(res.uid,"1")

          } else {
                  // this.navCtrl.setRoot('login',{
                  //     refresh:"true",
                  // });
           }




  })
      });
      }

      refresh(){
          if (this.navParams.get('refresh')=='true') {
              this.navCtrl.setRoot(this.navCtrl.getActive().component);
          }
      }
    presentAlert(id) {
        let alert: Alert = this.alertCtrl.create({
            title: id,

            buttons: ['Dismiss'],

        });
        alert.present();
    }

  ab() {
    this.navCtrl.push('SegmentsPage');

  }
    alertResend() {
        let alertt: Alert = this.alertCtrl.create({
            title: 'Resend Code',

            buttons: [{
                text: 'Cancel',
                role: 'cancel',

            },
                {
                    text:'Okay',handler: data =>{


                    }
                }],

        });
        alertt.present();
    }

  pr() {
    this.navCtrl.push('PrayerPage');

  }

  dv() {
    this.navCtrl.push('DevotionsPage');

  }

  nt() {
    this.navCtrl.push('NoticePage');

  }

  evn() {
    this.navCtrl.push('EventsTabsPage');

  }
  showOptions()
  {
      let modal = this.modalCtrl.create(ModalOption);
      modal.present();

  }
}



